* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #e9e9e9;
}

.contianer {
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.calculator {
  width: 415px;
  background-color: #e9e9e9;
  border-radius: 30px;
  padding: 40px;
  display: inline-block;
  box-shadow: -6px -6px 9px #fff, 11px 11px 21px -8px #000;
}

.calculator input {
  width: 93%;
  height: 45px;
  background-color: #e9e9e9;
  border: 0;
  border-radius: 10px;
  outline: 0;
  margin: 8px;
  font-size: 34px;
  box-shadow: inset -5px -7px 9px #fff, inset 5px 11px 17px -13px #000;
}

.calculator input:not(:lang(ae)):not(:lang(ar)):not(:lang(arc)):not(:lang(bcc)):not(:lang(bqi)):not(:lang(ckb)):not(:lang(dv)):not(:lang(fa)):not(:lang(glk)):not(:lang(he)):not(:lang(ku)):not(:lang(mzn)):not(:lang(nqo)):not(:lang(pnb)):not(:lang(ps)):not(:lang(sd)):not(:lang(ug)):not(:lang(ur)):not(:lang(yi)) {
  text-align: right;
}

.calculator input:is(:lang(ae), :lang(ar), :lang(arc), :lang(bcc), :lang(bqi), :lang(ckb), :lang(dv), :lang(fa), :lang(glk), :lang(he), :lang(ku), :lang(mzn), :lang(nqo), :lang(pnb), :lang(ps), :lang(sd), :lang(ug), :lang(ur), :lang(yi)) {
  text-align: left;
}

.color {
  color: #ff4500;
}

button {
  width: 60px;
  height: 60px;
  cursor: pointer;
  background-color: #e9e9e9;
  border: 0;
  border-radius: 50px;
  outline: 0;
  margin: 8px;
  font-size: 28px;
  font-weight: 500;
  box-shadow: -6px -6px 9px #fff, 11px 11px 21px -8px #000;
}

button:hover {
  background-color: #d6d6d6;
}

.zero {
  width: 145px;
}

