* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #e9e9e9;
}

.contianer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.calculator {
  width: 415px;
  display: inline-block;
  padding: 40px;
  border-radius: 30px;
  background-color: #e9e9e9;
  box-shadow: -6px -6px 9px #fff, 11px 11px 21px -8px #000;
}

.calculator input {
  width: 93%;
  height: 45px;
  margin: 8px;
  border-radius: 10px;
  font-size: 34px;
  text-align: end;
  border: 0;
  outline: 0;
  background-color: #e9e9e9;
  box-shadow: -5px -7px 9px #fff inset, 5px 11px 17px -13px #000 inset;
}

.color {
  color: #ff4500;
}

button {
  border-radius: 50px;
  font-size: 28px;
  font-weight: 500;
  background-color: #e9e9e9;
  width: 60px;
  height: 60px;
  margin: 8px;
  border: 0;
  box-shadow: -6px -6px 9px #fff, 11px 11px 21px -8px #000;
  cursor: pointer;
  outline: 0;
}

button:hover {
  background-color: #d6d6d6;
}

.zero {
  width: 145px;
}
